    description: 'ÏûëÏÑ±Ìïú Í∏ÄÏùò Î¨∏Ï≤¥, Ïñ¥Ï°∞, Í∞ÄÎèÖÏÑ±ÏùÑ AIÍ∞Ä Î∂ÑÏÑùÌïòÏó¨ Í∞úÏÑ†Ï†êÏùÑ Ï†úÏïàÌï©ÎãàÎã§.',
