    description: 'Í∏∞Ï°¥ Î¨∏Ïû•ÏùÑ Îçî Îß§Î†•Ï†ÅÏù¥Í≥† ÏûêÏó∞Ïä§ÎüΩÍ≤å Í∞úÏÑ†ÌïòÏó¨ Ï†úÏïàÌï©ÎãàÎã§.',
